import React, { Component } from 'react'
import TodoInput from './TodoInput'
import Todoing from './Todoing'
import './todo.css'



function getTodoList(){
    var todolist = [];
    let list = localStorage.getItem('todo');
    if(list != null){
        list = JSON.parse(list);
        for(var i=0;i<list.length;i++){
            if(list[i].done === 'false'){
                todolist.push(list[i].title);
            }
        }
    }
    
    return todolist;
}
function getFinishList(){
    var finishList = [];
    let list = localStorage.getItem('todo');
    if(list != null){
        list = JSON.parse(list);
        for(var i=0;i<list.length;i++){
            if(list[i].done === 'true'){
                finishList.push(list[i].title);
            }
        }
    }
    return finishList;
}
function getlocalStorage(){
    let list = localStorage.getItem('todo');
    list = JSON.parse(list);
    return list;
}
export default class TodoList extends Component {
    constructor(){
        super();
        this.state = {
            todo : [1,2,3],
            finish : [],
            todoListLength : 0,
            finishListLength : 0
        }
    }
    componentDidMount(){
        this.setState({
            todo : getTodoList(),
            finish : getFinishList(),
            todoListLength : getTodoList().length,
            finishListLength : getFinishList().length
        })
    }
    addItem=(data)=>{
        let list = getlocalStorage();
        list.unshift(data);
        localStorage.setItem('todo',JSON.stringify(list));
        this.componentDidMount();
    }
    delItem=(data)=>{
        var index = data;
        let list = getlocalStorage();
        list.splice(index,1);
        localStorage.setItem('todo',JSON.stringify(list));
        this.componentDidMount();
    }
    setFinish=(data,index)=>{
        let list = getlocalStorage();
        list[index].done = 'true';
        localStorage.setItem('todo',JSON.stringify(list));
        this.componentDidMount();
    } 
    setTodo=(data,index)=>{
        let list = getlocalStorage();
        list[index].done = 'false';
        localStorage.setItem('todo',JSON.stringify(list));
        this.componentDidMount();
    }
    render() {
        return (
            <div id="listborder">
                <TodoInput add={this.addItem} />
                <Todoing todo={this.state.todo} finish={this.state.finish} 
                         todoLength={this.state.todoListLength} finishLength={this.state.finishListLength}
                    end={this.delItem}  check={this.setFinish}   checkFinish={this.setTodo} />
            </div>
        )
    }
}
