import React, { Component } from 'react'
import './todo.css'
import $ from 'jquery'

function getIndex(title){
    var index;
    let list = localStorage.getItem('todo');
    list = JSON.parse(list);
    for(var i=0;i<list.length;i++){
        if(list[i].title == title){
            index = i;
        }
    }
    return index;
}

export default class Todoing extends Component {
    end(item){
        var index = getIndex(item.item);
        this.props.end(index);
    }
    check=(e,item)=>{
        var checked = e.target.checked;
        if(checked === true){
            var index = getIndex(item.item);
            this.props.check(item,index);
            var list = $(':checkbox');
            var idx;
            for(var i =0;i<list.length;i++){
                if(list[i].value === item.item && i != list.length-1){
                    idx = i;
                    $(':checkbox')[idx].checked = false;
                }
            }
            
        }
        else if(checked === false){
            var index = getIndex(item.item);
            this.props.checkFinish(item,index);
        }
    }
    render() {
        var {todo} = this.props;
        var {finish} = this.props;
        var {todoLength} = this.props;
        var {finishLength} = this.props;
        return (
            <div id="border">
                <p>正在进行<span id="number">{todoLength}</span></p>
                <ul id="add">
                    {
                        todo.map((item,index)=>{
                        return <li key={index}>
                                <input id="buttom" type="checkbox" value={item} onChange={(e)=>{this.check(e,{item})}} />
                                {item}
                                <button onClick={()=>{this.end({item})}}></button>
                            </li>
                        })
                    }
                </ul>
                <p>已经完成<span id="number">{finishLength}</span></p>
                <ul id="end">
                    {
                        finish.map((item,index)=>{
                            return <li key={index}>
                                <input id="buttom" type="checkbox" value={item} checked="checked" onChange={(e)=>{this.check(e,{item})}} />
                                {item}
                                <button onClick={()=>{this.end({item})}}></button>
                            </li>
                        })
                    }
                </ul>
            </div>
            
        )
    }
}
