import React, { Component } from 'react'
import './todo.css'
import $ from 'jquery'

export default class TodoInput extends Component {
    add=(e)=>{
        var value = e.target.value;
        if(e.keyCode === 13){
                var data = {title:value,done:'false'};
                this.props.add(data);
                $('#get').val("");
        }
    }
    render() {
        return (
            <div id="head">
            <div id="header">
                <span id="title">TodoList</span>
                <input type="text" onKeyDown={this.add} id="get" placeholder="添加Todo" />
            </div>
            </div>
        )
    }
}
